/* roboto-300 - latin */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 300;
  src: local('Roboto Light'), local('Roboto-Light'),
    url('./fonts/roboto-v20-latin-300.woff2') format('woff2'),
    /* Super Modern Browsers */ url('./fonts/roboto-v20-latin-300.woff')
      format('woff'); /* Modern Browsers */
}
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local('Roboto'), local('Roboto-Regular'),
    url('./fonts/roboto-v20-latin-regular.woff2') format('woff2'),
    /* Super Modern Browsers */ url('./fonts/roboto-v20-latin-regular.woff')
      format('woff'); /* Modern Browsers */
}
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local('Roboto Bold'), local('Roboto-Bold'),
    url('./fonts/roboto-v20-latin-700.woff2') format('woff2'),
    /* Super Modern Browsers */ url('./fonts/roboto-v20-latin-700.woff')
      format('woff'); /* Modern Browsers */
}
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 900;
  src: local('Roboto Black'), local('Roboto-Black'),
    url('./fonts/roboto-v20-latin-900.woff2') format('woff2'),
    /* Super Modern Browsers */ url('./fonts/roboto-v20-latin-900.woff')
      format('woff'); /* Modern Browsers */
}

body {
  margin: 0;
  font-family: 'Roboto';
}
